export const allGoodsReducer = (state = initialState, action: AllGoodsActionsTypes): typeof initialState => {
  switch(action.type) {
    case 'allGoods/TEST': {
      return {
        ...state
      };
    }
    default: 
      return state;
  }
}


export type AllGoodsActionsTypes = InferActionsTypes<typeof allGoodsActions>

